<template>
  <div>
    <div class="lr-form">
      <div class="lr-form-item">
        <mt-field label placeholder="账号" type="number" v-model="form_data.account"></mt-field>
      </div>
      <div class="lr-form-item">
        <mt-field label placeholder="密码" type="password" v-model="form_data.password"></mt-field>
      </div>
      <div class="form-item-button form-item-button-single">
        <mt-button type="primary" :disabled="form_submiting" @click.native="handleSubmit">登录</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui'
import http from '@/utils/axios'
export default {
  data () {
    return {
      form_submiting: false,
      form_data: {
        account: '',
        password: ''
      }
    }
  },
  methods: {
    handleSubmit () {
      if (this.form_submiting) {
        return
      }
      this.form_submiting = true
      if (!this.form_data.account || !this.form_data.password) {
        Toast('账号密码必须填写')
        this.form_submiting = false
        return
      }
      http.ajaxPost('/v1/login', { ...this.form_data }, res => {
        this.form_submiting = false
        if (res.data.status == 200) {
          localStorage.setItem('token', res.data.data.access_token)
          this.$router.push('/')
        } else {
          Toast(res.data.message)
        }
      })
    }
  },
  created () {
    if (localStorage.getItem('token')) {
      this.$router.push('/')
    }
  }
}
</script>
<style scoped>
.lr-form-item {
  margin: 1em 0px;
}
</style>



